.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  color: white;
  padding: 20px;
}

.characters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  max-width: 1200px;
  width: 100%;
  margin-top: 20px;
}

.character-card {
  background-color: #3c3e44;
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s;
  cursor: pointer;
}

.character-card:hover {
  transform: translateY(-5px);
}

.character-card img {
  width: 100%;
  height: auto;
}

.character-card h3 {
  margin: 10px 0;
  font-size: 18px;
}

.character-card p {
  margin: 5px 0 15px;
  font-size: 14px;
}

.pagination {
  margin-top: 30px;
  display: flex;
  align-items: center;
  gap: 20px;
}

.pagination button {
  background-color: #97ce4c;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.pagination button:disabled {
  background-color: #666;
  cursor: not-allowed;
}

.character-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 600px;
  width: 100%;
}

.character-details img {
  width: 250px;
  height: 250px;
  border-radius: 10px;
  margin: 20px 0;
}

.character-info {
  background-color: #3c3e44;
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  margin-bottom: 20px;
  text-align: left;
}

.character-details button {
  background-color: #ff9800;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  margin-top: 20px;
}

.error {
  color: #ff5252;
  background-color: rgba(255, 82, 82, 0.1);
  padding: 10px;
  border-radius: 5px;
  width: 100%;
  max-width: 600px;
  margin-top: 20px;
}